@import '../_sass/jekyll-theme-midnight.scss';

.wrapper {
  max-width: 800px;
}

section {
  margin-top: 0px;
  max-width: 800px;

  #title {
    margin: 0;
    padding: 0 0 5px 0;

    & > div {
      display: flex;
    }

    h1 {
      a {
        color: #fff;
      }
    }

    h1,
    p {
      text-align: left;
    }

    p {
      margin-bottom: 10px;
      font-size: 16px;
      font-style: italic;
    }

    ul.social-media-list {
      display: inline-block;
      flex-grow: 1;
      margin: 0;
      padding: 0;
      text-align: right;

      & > li {
        margin: 0 0 0 15px;
        list-style: none;
        display: inline-block;
        font-size: 16px;
      }
    }

    .svg-icon {
      width: 18px;
      height: 18px;
      display: inline-block;
      fill: #f0f0f0;
      padding-right: 0;
      margin-top: 4px;
      vertical-align: text-top;
    }
  }
}

body {
  line-height: 1.75;
}

code {
  background-color: #444;
  padding: 3px 4px;
}

pre {
  box-shadow: none;
  border-radius: 4px;

  code {
    background-color: transparent;
  }
}

.related-posts {
  &.post-list-heading {
    font-size: 18px;
  }

  &.post-list h3 {
    font-size: 16px;
  }
}

.post-list {
  h3 {
    margin-bottom: 0px;
  }

  .post-meta {
    display: inline-block;
    margin-bottom: 5px;
    color: #c0b7a5;
  }
}

.tags {
  margin-top: 40px;
  margin-bottom: 15px;
}

.footer {
  display: flex;
  flex-direction: row;

  .page-links {
    margin: 0;
    padding: 0;
    list-style: none;
    flex-grow: 1;
    text-align: right;

    li {
      display: inline-block;
      margin-left: 15px;
    }
  }
}
